___CSS_LOADER_EXPORT___.push([module.id, `.task-details.active{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    border-left: 1px solid #3c3c3c;
    height: 100%;
}

.task-details>hr{
    width: 100%;
    margin: 0;
    padding: 0;
    color:  #484848;
    opacity: 0.1;

}

.task-header>img{
    height: 1.7rem;
    width: 1.7rem;
    padding: .5rem;
    filter: invert(23%) sepia(71%) saturate(0%) hue-rotate(249deg) brightness(100%) contrast(10%);
    cursor: pointer;
    border-radius: .5rem;
}

.task-header>img:hover{
    background-color: #b4b4b432;
}

.task-header{
    display: flex;
    gap: 1rem;
}
.task-header>.task-content{
    flex: 1;
    font-size: 1.1rem;
    background-color: transparent;
    color: #ffffff;
    border: none;
    outline: none;
    font-weight: bold;
}


.task-date-status{
    display: flex;
    gap: 1rem;
    align-items: center;
    padding: 1rem 2rem;
}

.task-date-status>.task-checkbox{
    min-width: 1.5rem;
    height: 1.5rem;
    border-radius: 0.3rem;
    cursor: pointer;
}

.task-date-status>input[type="date"]{
    flex: 1;
    background-color: transparent;
    color: #3c74f9;
    border: none;
    outline: none;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
    border-radius: .5rem;
    padding: .5rem;
    
}

.task-date-status>input[type="date"]:hover{
    background-color: #85858541;
}



.task-description{
    flex: 1 1 10%;
    height: 100%;
    font-size: 1rem;
    background-color: transparent;
    color: #ffffff;
    border: none;
    resize: none;
}

.task-header-description{
    display: flex;
    flex: 1;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 2rem;
}

.task-description::placeholder{
    color: #C2C2C2;
}

.task-description:focus-visible{
    outline: none;
}

.task-checklist-container{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-height: 50vh;
    overflow-y: auto;
}

.task-checklist-container>.checklist-item{
    display: flex;
    gap: 1rem;
    align-items: center;
    padding-bottom: 1rem;
    border-bottom: .0625rem solid #3c3c3c;
}

.task-checklist-container>.checklist-item:first-child{
    padding-top: 1rem;
    border-top: .0625rem solid #3c3c3c;
}


.task-checklist-container>.checklist-item:last-child{
    border-bottom: none;
    margin-bottom: 10%;
}

.checklist-content{
    flex: 1;
    font-size: 1rem;
    background-color: transparent;
    color: #ffffff;
    border: none;
    outline: none;
    padding: 0.2rem;
}

.checklist-checkbox{
    min-width: 1.2rem;
    height: 1.2rem;
    border-radius: 0.3rem;
    cursor: pointer;
}

.checklist-item>img{
    filter: invert(23%) sepia(71%) saturate(0%) hue-rotate(249deg) brightness(100%) contrast(10%);
    height: 1.5rem;
    width: 1.5rem;
    cursor: pointer;
    border-radius: .4rem;
    transition: background 0.3s;
    padding: .15rem;
}

.checklist-item>img:hover{
    background-color: #b4b4b432;
}


.task-actions{
    display: flex;
    align-items: center;
    gap: 1rem;
}

#list-selection{
    font-size: 1rem;
    font-weight: bold;
    background-color: #676767;
    border-radius: .5rem;
    padding: .5rem;
    cursor: pointer;
    transition: background 0.3s;
    margin-right: auto;
}

#list-selection:hover{
    background-color: #858585;
}

.task-actions>img{
    filter: invert(23%) sepia(71%) saturate(0%) hue-rotate(249deg) brightness(100%) contrast(10%);
    height: 2rem;
    width: 2rem;
    cursor: pointer;
    border-radius: .5rem;
    transition: background 0.3s;
    padding: .2rem;
}

.task-actions>img:hover{
    background-color: #b4b4b432;
}

.task-details.empty{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #3c3c3c;
}

.select-task-illustration{
    width:10rem;
    
}

.select-task-caption{
    text-wrap: nowrap;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
    color: #858585;
}


.unselectable {
    pointer-events:none;
}`, "",{"version":3,"sources":["webpack://./src/css/task-details.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,SAAS;IACT,UAAU;IACV,eAAe;IACf,YAAY;;AAEhB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,cAAc;IACd,6FAA6F;IAC7F,eAAe;IACf,oBAAoB;AACxB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,SAAS;AACb;AACA;IACI,OAAO;IACP,iBAAiB;IACjB,6BAA6B;IAC7B,cAAc;IACd,YAAY;IACZ,aAAa;IACb,iBAAiB;AACrB;;;AAGA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,OAAO;IACP,6BAA6B;IAC7B,cAAc;IACd,YAAY;IACZ,aAAa;IACb,iBAAiB;IACjB,iBAAiB;IACjB,eAAe;IACf,oBAAoB;IACpB,cAAc;;AAElB;;AAEA;IACI,2BAA2B;AAC/B;;;;AAIA;IACI,aAAa;IACb,YAAY;IACZ,eAAe;IACf,6BAA6B;IAC7B,cAAc;IACd,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,OAAO;IACP,sBAAsB;IACtB,SAAS;IACT,kBAAkB;AACtB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,oBAAoB;IACpB,qCAAqC;AACzC;;AAEA;IACI,iBAAiB;IACjB,kCAAkC;AACtC;;;AAGA;IACI,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,OAAO;IACP,eAAe;IACf,6BAA6B;IAC7B,cAAc;IACd,YAAY;IACZ,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,6FAA6F;IAC7F,cAAc;IACd,aAAa;IACb,eAAe;IACf,oBAAoB;IACpB,2BAA2B;IAC3B,eAAe;AACnB;;AAEA;IACI,2BAA2B;AAC/B;;;AAGA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,yBAAyB;IACzB,oBAAoB;IACpB,cAAc;IACd,eAAe;IACf,2BAA2B;IAC3B,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,6FAA6F;IAC7F,YAAY;IACZ,WAAW;IACX,eAAe;IACf,oBAAoB;IACpB,2BAA2B;IAC3B,cAAc;AAClB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,8BAA8B;AAClC;;AAEA;IACI,WAAW;;AAEf;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,cAAc;AAClB;;;AAGA;IACI,mBAAmB;AACvB","sourcesContent":[".task-details.active{\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: column;\n    border-lef